<template>
    <div class="recordList">
        <div class="list-item" v-for="(item,index) in listArr" :key="index" @click="goDetail(item.base64RcordId)">
            <div class="item-left" >
              <div class="time">洗消日期：{{item.approveTime}}</div>
              <h3>外出原因：{{splitReason(item.reason)}}</h3>
            </div>
          <div class="item-right">
            <div class="state" v-if="item.approveStatusIndex==1 && item.billStatusIndex==1"><span class="text">返场消毒拍照</span></div>
            <span class="item-icon"></span>
          </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "SafeRecordList",
        data() {
            return {}
        },
        props:{
          listArr:{
            type:Array,
            default:()=>{
              return [];
            }
          }
        },
      mounted(){
      },
        methods: {
          splitReason(data){
            if(data){
              if(data.split('').length>32){
                return  data.split('').splice(1,32).join('');
              }else{
                return data;
              }
            }else {
              return '无'
            }
          },
          goDetail(recordId){
            this.$router.push('/goOutDetail?rcordId='+recordId);
          }
        }
    }
</script>

<style scoped lang="scss">
  .list-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: .12rem .1rem;
    background-color: #fff;
    border-bottom: 1px solid #dddddd;

    .item-left {
      width: 70%;
      .time{
        color: #8B8B8D;
        font-size: 12px;
        padding-bottom: 4px;
      }
      h3{
        color: #06121E;
        font-size: .14rem;
        padding-bottom: 5px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space:normal;
        max-height: 40px;
      }
      .name{
        color: #06121E;
        font-size: 12px;
      }
    }

    .item-right {
      display: flex;
      align-items: center;
      font-size: .14rem;
      .state{
        width: .9rem;
        height: .28rem;
        border-radius: 14px;
        background: #FEE5E5;
        color:#EE6363 ;
        display: table;
        font-size: 12px;
        .text{
          display: table-cell;
          text-align: center;
          vertical-align: middle;
        }
      }
      .item-icon {
        display: inline-block;
        margin-left: 0.19rem;
        width: .1rem;
        height: .1rem;
        background: url('~@/assets/icon-next.png') no-repeat;
        background-size: 100%;
        transform: rotate(-90deg);
        opacity: 0.4;
      }
    }

  }

</style>



