<template>
  <div style="max-width: 100%;box-sizing: border-box;" class="container">
    <mt-header title="外出及返场洗消记录" fixed>
      <router-link to="/addGoOutRecord" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
      <mt-button slot="right" @click="showSearch">查询</mt-button>
    </mt-header>
    <div class="mt4 list-block">
      <mt-loadmore :bottom-method="loadBottom"
                   @bottom-status-change="handleBottomChange"
                   :auto-fill="false"
                   :bottom-all-loaded="allLoaded" ref="loadmore">
        <safe-record-list  :listArr="listData"></safe-record-list>
      </mt-loadmore>
    </div>
    <mt-popup
      v-model="popupVisible"
      position="top">
      <div class="popBox">
          <h3>请选择查询日期</h3>
          <div class="during-time">
            <span @click="selectStartDate">{{startTimeText}}</span>
            <span style="color: #555;">至</span>
            <span @click="selectEndDate">{{endTimeText}}</span>
          </div>
          <div class="bottom">
              <span class="cancel" @click="hideModal">取消</span>
              <span class="search" @click="search()">查询</span>
          </div>
      </div>
    </mt-popup>
    <mt-datetime-picker
      ref="startTimePicker"
      type="date"
      v-model="startTime"
      @confirm="startTimeConfirm">
    </mt-datetime-picker>

    <mt-datetime-picker
      ref="endTimePicker"
      type="date"
      :startDate="beginTime"
      v-model="endTime"
      @confirm="endTimeConfirm">
    </mt-datetime-picker>

  </div>
</template>
<script>
  import myHeader from "@/components/Header";
  import safeRecordList from  "@/components/SafeRecordList"
  import { formatDate} from '@/utils/tool'
  import {getBiologySafeList} from  '@/api/personGoOut'
  import { Indicator } from 'mint-ui'
  import {mapMutations,mapGetters} from 'vuex'
  export default {
    name:'clearRecord',
    components:{
      myHeader,
      safeRecordList
    },
    data(){
      return{
        startTimeText:formatDate(new Date(), 'yyyy-MM-dd'),
        endTimeText:formatDate(new Date(), 'yyyy-MM-dd'),
        startTime:formatDate(new Date(), 'yyyy-MM-dd'),
        endTime:formatDate(new Date(), 'yyyy-MM-dd'),
        beginTime:new Date(),
        popupVisible:false,
        listData:[],
        allLoaded: false,
        bottomStatus: '',
        pageNumber: 1,
        pageSize: 10,
      }
    },
    created(){
      this.getDataList({
        pageNumber:1,
        pageSize: this.pageSize
      });
    },
    beforeRouteEnter(to,from,next){
      next(vm=>{
        if (from.name === 'GoOutDetail') {
          vm.$store.commit('CLEAR_PHOTOLIST',[]);
          sessionStorage.setItem('photoList',[]);
          vm.$store.commit('CLEAR_ALLPHOTOLIST',[]);
          sessionStorage.setItem('allPhotoLists',[]);
        }
      });
    },
    methods:{
      loadBottom() {
        console.log('下来');
        this.pageNumber++;
        getBiologySafeList({
          pageNumber: this.pageNumber,
          pageSize: this.pageSize,
        }).then((res) => {
          let newList = res.data;
          if (newList.length < this.pageSize) {
            this.allLoaded = true;
          } else {
            this.allLoaded = false;
          }

          this.listData = [...this.listData, ...newList];

          this.$refs.loadmore.onBottomLoaded();

        }, (err) => {
          this.allLoaded = true;
        });
      },
      handleBottomChange(status) {
        this.bottomStatus = status;
      },
      getDataList(data){
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        getBiologySafeList(data).then((res)=>{
          Indicator.close();
          if(res.status===200){
            this.listData=res.data;
            if (this.listData.length < this.pageSize) {
              this.allLoaded = true;
            }
          }
        }).catch((err)=>{
          Indicator.close();
        })
      },
      showSearch(){
        this.popupVisible=true;
      },
      hideModal(){
        this.popupVisible=false;
      },
      selectStartDate(){
        this.$refs.startTimePicker.open();
      },
      selectEndDate(){
        this.$refs.endTimePicker.open();
      },
      startTimeConfirm(date){
        this.startTimeText= formatDate(date,'yyyy-MM-dd');
        this.beginTime=new Date(date.getTime());
      },
      endTimeConfirm(date){
        this.endTimeText= formatDate(date,'yyyy-MM-dd');
      },
      search(){
        this.getDataList({
          pageNumber:1,
          pageSize: this.pageSize,
          startDate:this.startTimeText+" 00:00:00",
          endDate:this.endTimeText+" 23:59:59",
        });
        this.popupVisible=false;
      }
    }
  }
</script>
<style scoped lang="scss">
  .container {
    min-height: 100vh;
    background-color: #f8f8f8;
    padding: 0.12rem 0;
    .mint-popup-top{
      top:10%;
      border-radius:.14rem ;
    }
    .popBox{
      width: 3.17rem;
      height: 1.72rem;
      font-size: 18px;
      text-align: center;
      padding-top: .21rem;
      position: relative;
      h3{
        color: #06121E;
      }
      .during-time{
        background: #F6F6F6;
        width: 80%;
        height: .45rem;
        margin:.11rem auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding:0 .15rem;
        font-size: .16rem;
        color: #000000;
        border-radius: 6px;
      }
      .bottom{
        position: absolute;
        bottom: 0;
        left: 0;
        border-top: 1px solid #dddddd;
        width: 100%;
        height: .5rem;
        line-height: .5rem;
        display: flex;
        justify-content: center;
        span{
          display: inline-block;
          width: 48%;
          text-align: center;
        }
        .cancel{
          color:#06121E;
          border-right: 1px solid #ddd;
        }
        .search{
          color: #108EE9;
        }
      }
    }
  }
</style>



